html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
  padding-inline-start: 0;
}

body.modal-open {
  overflow: hidden;
}

.backdrop {
  z-index: 160;
  width: 100%;
  height: 100%;
  background-color: var(--background-mobile-menu-color);
  opacity: 1;
  transition: opacity var(--time-duration) var(--timing-function);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop.is-hidden .modal {
  z-index: 160;
  transform: translate(-50%, -100%)scale(.7);
}

.modal {
  width: 280px;
  z-index: 6;
  max-height: 100vh;
  background-color: #fff;
  border-radius: 4px;
  padding: 48px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 4px #00000040;
}

.modal-close-button {
  width: 30px;
  height: 30px;
  box-shadow: none;
  cursor: pointer;
  transition: background-color var(--time-duration) var(--timing-function);
  background-color: #fff;
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  padding-right: 0;
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal-close-button:hover, .modal-close-button:focus {
  background-color: #ff6b08;
}

.modal-close-button-svg {
  fill: #000;
}

.current__content {
  width: 240px;
  height: 357px;
  background-color: #818181;
  display: block;
  position: relative;
  overflow: hidden;
}

.current_image {
  display: block;
}

.modal-ul {
  list-style: none;
}

.modal-button-wrapper {
  justify-content: space-between;
  display: flex;
}

.modal-button {
  width: 112px;
  background-color: #fff;
  border-radius: 5px;
}

.modal-btn-orange {
  color: #fff;
  background-color: #ff6b01;
  border: 0;
}

@media screen and (min-width: 767px) {
  .modal {
    width: 618px;
    height: 562px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 882px;
    height: 568px;
  }
}

